body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  transition: background 0.3s ease;
}

#root {
  min-height: 100vh;
  background: radial-gradient(circle at center, rgba(255,255,255,0.05) 0%, transparent 70%);
}

body[data-theme='dark'] {
  background: linear-gradient(135deg, #0f1729 0%, #1a1f35 50%, #2a3447 100%);
  color: #ffffff;
  position: relative;
}

body[data-theme='dark']::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 0% 0%, rgba(45,156,219,0.15) 0%, transparent 50%),
              radial-gradient(circle at 100% 100%, rgba(255,89,89,0.15) 0%, transparent 50%);
  pointer-events: none;
}

.app-title {
  background: linear-gradient(45deg, #FF512F, #F09819, #FF8C42, #FFC75F, #FF512F);
  background-size: 400% auto;
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  font-weight: 900;
  letter-spacing: -1.5px;
  cursor: pointer;
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
  animation: shine 8s linear infinite;
  text-shadow: 3px 3px 6px rgba(0, 0, 0, 0.15);
  position: relative;
  font-size: 2.8rem;
  padding: 0.5rem 0;
  filter: drop-shadow(0 0 8px rgba(255, 140, 66, 0.3));
}

.youtube-player-container {
  position: relative;
  background: rgba(255, 255, 255, 0.07);
  border-radius: 32px;
  padding: 40px;
  margin: 35px 0;
  backdrop-filter: blur(25px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.2),
              0 10px 25px rgba(0, 0, 0, 0.1),
              inset 0 2px 2px rgba(255, 255, 255, 0.1);
  transition: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);
}

.youtube-player-container::before {
  content: '';
  position: absolute;
  inset: 0;
  border-radius: 32px;
  padding: 2px;
  background: linear-gradient(45deg, rgba(255,255,255,0.1), rgba(255,255,255,0.2));
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  pointer-events: none;
}

.playlist-item {
  margin: 20px 0;
  padding: 25px;
  border-radius: 24px;
  background: rgba(255, 255, 255, 0.05);
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  border: 1px solid rgba(255, 255, 255, 0.08);
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.playlist-item:hover {
  transform: translateX(15px) scale(1.03);
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.15),
              inset 0 1px 2px rgba(255, 255, 255, 0.2),
              0 0 20px rgba(255, 140, 66, 0.1);
}

::-webkit-scrollbar {
  width: 10px;
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  border: 2px solid transparent;
  background-clip: padding-box;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.2);
  border: 2px solid transparent;
  background-clip: padding-box;
}

.app-title {
  background: linear-gradient(45deg, #FF512F, #F09819, #FF8C42, #FFC75F, #FF512F);
  background-size: 400% auto;
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  font-weight: 900;
  letter-spacing: -1.5px;
  cursor: pointer;
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
  animation: shine 8s linear infinite;
  text-shadow: 3px 3px 6px rgba(0, 0, 0, 0.15);
  position: relative;
  font-size: 2.5rem;
  padding: 0.5rem 0;
}

.app-title:hover {
  opacity: 1;
  transform: scale(1.05) translateY(-3px);
  text-shadow: 0 0 40px rgba(255, 140, 66, 0.6);
  letter-spacing: -0.5px;
  animation: shine 4s linear infinite;
}

.app-title::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 0;
  width: 100%;
  height: 3px;
  background: linear-gradient(90deg, transparent, #FF8C42, transparent);
  transform: scaleX(0);
  transition: transform 0.5s cubic-bezier(0.4, 0, 0.2, 1);
  filter: blur(1px);
}

.app-title::before {
  content: '';
  position: absolute;
  top: -8px;
  left: 0;
  width: 100%;
  height: 3px;
  background: linear-gradient(90deg, transparent, #FF512F, transparent);
  transform: scaleX(0);
  transition: transform 0.5s cubic-bezier(0.4, 0, 0.2, 1);
  filter: blur(1px);
}

.app-title:hover::after,
.app-title:hover::before {
  transform: scaleX(1);
}

.youtube-player-container {
  position: relative;
  background: rgba(255, 255, 255, 0.07);
  border-radius: 28px;
  padding: 35px;
  margin: 30px 0;
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.2),
              0 10px 25px rgba(0, 0, 0, 0.1),
              inset 0 1px 1px rgba(255, 255, 255, 0.1);
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.youtube-player-container:hover {
  transform: translateY(-10px) scale(1.02);
  box-shadow: 0 25px 70px rgba(0, 0, 0, 0.3),
              0 15px 35px rgba(0, 0, 0, 0.2),
              inset 0 2px 2px rgba(255, 255, 255, 0.15);
}

.playlist-container {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 28px;
  padding: 35px;
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.12);
  box-shadow: 0 15px 50px rgba(0, 0, 0, 0.2),
              inset 0 1px 1px rgba(255, 255, 255, 0.1);
  height: calc(100vh - 200px);
  overflow-y: auto;
  transition: all 0.5s ease;
}

.playlist-item {
  margin: 18px 0;
  padding: 22px;
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.05);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  border: 1px solid rgba(255, 255, 255, 0.08);
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.playlist-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, transparent, rgba(255, 255, 255, 0.08), transparent);
  transform: translateX(-100%);
  transition: transform 0.6s ease;
}

.playlist-item:hover {
  transform: translateX(15px) scale(1.03);
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.15),
              inset 0 1px 2px rgba(255, 255, 255, 0.2);
}

.playlist-item:hover::before {
  transform: translateX(100%);
}

@keyframes shine {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

.video-info {
  padding: 15px;
  margin-top: 15px;
  background: rgba(255,255,255,0.05);
  border-radius: 12px;
  border: 1px solid rgba(255,255,255,0.1);
}

@keyframes gradient {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

.playlist-container {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 16px;
  padding: 20px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  height: calc(100vh - 200px);
  overflow-y: auto;
}

.playlist-item {
  margin: 10px 0;
  padding: 15px;
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.05);
  transition: all 0.3s ease;
  cursor: pointer;
}

.playlist-item:hover {
  transform: translateX(10px);
  background: rgba(255, 255, 255, 0.1);
}
